<script>
// import Worker from '../../../../static/work/test.worker.js'
import tuopuInfo from "../../../../static/dailyOperationTuoPuOrder";
import backgroundImage from "@/assets/DailyOperation/background/3-1.jpg";
// import FlipClock from '@/components/FlipClock'
import echartsTuoPu from "@/components/echartsTuoPu";
import websockObject from "@/mixins/websock";

export default {
  name: "rightHeader",
  mixins: [websockObject],
  data() {
    return {
      backgroundImage,
      leftErrorInfo: [],
      rightErrorInfo: [],
      tillTimeObject: null,
      tillTime: "",
      num: 0
    };
  },
  components: {
    // FlipClock,
    echartsTuoPu
  },
  beforeDestroy() {
    this.tillTimeObject && clearInterval(this.tillTimeObject);
  },
  mounted() {
    this.tillTimeObject && clearInterval(this.tillTimeObject);
    let _this = this;
    this.tillTimeObject = setInterval(() => {
      _this.tillTime = _this.Format("yyyy-MM-dd HH:mm:ss");
    }, 1000);
    this.$nextTick(() => {
      // eslint-disable-next-line no-undef
      _this.wsuri = BaseUrl.dailyRightHeader.websockUrl;
    });
    // this.useTheTestDate()
  },
  methods: {
    useTheTestDate() {
      let testError = [
        {
          from: "算法服务2",
          to: "中间件as、ar2",
          belong: "line",
          type: "right",
          state: 1
        },
        {
          from: "期货行情",
          to: "中间件as、ar2",
          belong: "line",
          type: "right",
          state: 1
        },
        {
          from: "中间件as、ar2",
          to: "期货行情",
          belong: "line",
          type: "right",
          state: 1
        },
        {
          type: "left",
          belong: "device",
          name: "集中交易KCXP1",
          state: 1
        }
      ];
      this.getWebsocketMsg(testError);
    },
    handerDate(element) {
      let historyDate =
        element.type === "left"
          ? JSON.parse(JSON.stringify(this.leftErrorInfo))
          : JSON.parse(JSON.stringify(this.rightErrorInfo));
      let target = historyDate.findIndex(item => {
        return item.key === element.key;
      });
      if (target === -1 && element.state !== 0) {
        historyDate.push(element);
      }
      if (target !== -1 && element.state === 0) {
        historyDate.splice(target, 1);
      }
      switch (element.type) {
        case "left":
          this.leftErrorInfo = historyDate;
          break;

        default:
          this.rightErrorInfo = historyDate;
          break;
      }
    },
    getWebsocketMsg(sourDate) {
      let toRefresh = [];
      if (sourDate.length > 0) {
        sourDate.forEach(element => {
          let ItemKey =
            element.belong === "device"
              ? element.name
              : element.from + "To" + element.to;
          element.key = ItemKey;
          this.handerDate(element);
          if (!toRefresh.includes(element.type)) toRefresh.push(element.type);
        });
      }
      let _this = this;
      if (toRefresh.includes("left")) {
        this.leftTime && clearTimeout(this.leftTime);
        this.leftTime = setTimeout(() => {
          _this.$refs.left.initChart(this.leftErrorInfo);
        }, 500);
      }
      if (toRefresh.includes("right")) {
        this.rightTime && clearTimeout(this.rightTime);
        this.rightTime = setTimeout(() => {
          _this.$refs.right.initChart(this.rightErrorInfo);
        }, 500);
      }
      return new Promise(function(resolve, reject) {
        resolve();
      });
    }
  },
  render(h) {
    return (
      <section
        class="mainBox"
        {...{
          style: { backgroundImage: " url(" + this.backgroundImage + ")" }
        }}
      >
        <section class="timeClock">{this.tillTime}</section>
        <section class="contentBox flexBox">
          <section class="flexBoxItem">
            <div class="title">{tuopuInfo[0].title}</div>
            <section class="chartsBox">
              <echartsTuoPu
                ref="left"
                dataType={tuopuInfo[0].node}
              ></echartsTuoPu>
            </section>
          </section>
          <section class="flexBoxItem">
            <div class="title">{tuopuInfo[1].title}</div>
            <section class="chartsBox">
              <echartsTuoPu
                ref="right"
                dataType={tuopuInfo[1].node}
              ></echartsTuoPu>
            </section>
          </section>
        </section>
      </section>
    );
  }
};
</script>

<style scoped>
.timeClock {
  position: absolute;
  right: 59px;
  top: 160px;
  font-size: 78px;
  color: #e0e8ff;
  font-weight: bold;
}
.contentBox {
  width: 1800px;
  height: 760px;
  position: absolute;
  bottom: 0;
  left: 40px;
}
.flexBox {
  display: flex;
}
.flexBoxItem {
  width: 50%;
  height: 760px;
  padding: 15px;
  box-sizing: border-box;
}
.title {
  font-size: 30px;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0px;
  color: #e6f4ff;
  text-align: left;
}
.chartsBox {
  width: 100%;
  height: 700px;
}
</style>



