<script>
import tuopuInfo from "../../../../static/dailyOperationTuoPuOrder";
import backgroundImage from "@/assets/DailyOperation/background/3-2.jpg";
import FlipClock from "@/components/FlipClock";
import echartsTuoPu from "@/components/echartsTuoPu";
import websockObject from "@/mixins/websock";
export default {
  name: "rightCenter",
  mixins: [websockObject],
  data() {
    return {
      backgroundImage,
      leftErrorInfo: [],
      rightErrorInfo: []
    };
  },
  components: {
    FlipClock,
    echartsTuoPu
  },
  mounted() {
    let _this = this;
    this.$nextTick(() => {
      // eslint-disable-next-line no-undef
      _this.wsuri = BaseUrl.dailyRightCenter.websockUrl;
    });
    // this.useTheTestDate()
  },
  methods: {
    useTheTestDate() {
      this.getWebsocketMsg([
        {
          type: "right",
          belong: "line",
          state: 1,
          from: "DB2",
          to: "行情前置机1"
        },
        {
          type: "right",
          belong: "line",
          state: 1,
          from: "行情前置机1",
          to: "DB1"
        },
        {
          type: "right",
          belong: "line",
          state: 1,
          from: "行情前置机1",
          to: "算法服务2"
        }
      ]);
    },
    handerDate(element) {
      let historyDate =
        element.type === "left"
          ? JSON.parse(JSON.stringify(this.leftErrorInfo))
          : JSON.parse(JSON.stringify(this.rightErrorInfo));
      let target = historyDate.findIndex(item => {
        return item.key === element.key;
      });
      if (target === -1 && element.state !== 0) {
        historyDate.push(element);
      }
      if (target !== -1 && element.state === 0) {
        historyDate.splice(target, 1);
      }
      switch (element.type) {
        case "left":
          this.leftErrorInfo = historyDate;
          break;

        default:
          this.rightErrorInfo = historyDate;
          break;
      }
    },
    getWebsocketMsg(sourDate) {
      let toRefresh = [];
      if (sourDate.length > 0) {
        sourDate.forEach(element => {
          let ItemKey =
            element.belong === "device"
              ? element.name
              : element.from + "To" + element.to;
          element.key = ItemKey;
          this.handerDate(element);
          if (!toRefresh.includes(element.type)) toRefresh.push(element.type);
        });
      }
      // if (toRefresh.includes('left')) {
      //   this.$refs.left.initChart(this.leftErrorInfo)
      // }
      // if (toRefresh.includes('right')) {
      //   this.$refs.right.initChart(this.rightErrorInfo)
      // }
      let _this = this;
      if (toRefresh.includes("left")) {
        this.leftTime && clearTimeout(this.leftTime);
        this.leftTime = setTimeout(() => {
          _this.$refs.left.initChart(this.leftErrorInfo);
        }, 500);
      }
      if (toRefresh.includes("right")) {
        this.rightTime && clearTimeout(this.rightTime);
        this.rightTime = setTimeout(() => {
          _this.$refs.right.initChart(this.rightErrorInfo);
        }, 500);
      }
      return new Promise(function(resolve, reject) {
        resolve();
      });
    }
  },
  render(h) {
    return (
      <section
        class="mainBox"
        {...{
          style: { backgroundImage: " url(" + this.backgroundImage + ")" }
        }}
      >
        <section class="contentBox flexBox">
          <section class="flexBoxItem">
            <div class="title">{tuopuInfo[2].title}</div>
            <section class="chartsBox">
              <echartsTuoPu
                ref="left"
                dataType={tuopuInfo[2].node}
              ></echartsTuoPu>
            </section>
          </section>
          <section class="flexBoxItem">
            <div class="title">{tuopuInfo[3].title}</div>
            <section class="chartsBox">
              <echartsTuoPu
                ref="right"
                dataType={tuopuInfo[3].node}
              ></echartsTuoPu>
            </section>
          </section>
        </section>
      </section>
    );
  }
};
</script>

<style scoped>
.timeClock {
  position: absolute;
  right: 50px;
  top: 245px;
}
.contentBox {
  width: 1825px;
  height: 970px;
  position: absolute;
  top: 40px;
  left: 40px;
}
.flexBox {
  display: flex;
}
.flexBoxItem {
  width: 50%;
  height: 970px;
  padding: 15px;
  box-sizing: border-box;
}
.title {
  font-size: 30px;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0px;
  color: #e6f4ff;
  text-align: left;
}
.chartsBox {
  width: 100%;
  height: 100%;
}
</style>



